import React, {useState} from 'react';
import './Feedback.css'
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import { Button } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import StarRatings from 'react-star-ratings';
import axios from 'axios';
import { lightGreen } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'relative',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  Body1: {
    width: '500px',
    height: '465px',
    display: 'block',
    margin: '250px auto',
    [theme.breakpoints.down('sm')]: {
      width: '350px',
      height: '430px',
      padding: '5px',
      margin: '185px auto'
  },
  
  },
  StarRating: {
    width: '71%',
    margin: '25px auto',
    [theme.breakpoints.down('sm')]: {
      width: '95%',
      margin: '20px auto'
    },
  },
  Button: {
    backgroundColor: 'lightseagreen',
    '&:hover': {
      background: 'rgb(12, 62, 60)',
      color:'white'
    }
  }
}));

export default function FeedbackModal() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState({comment: '', name: '', stars: 1})
  const [messageSent, setMessageSent] = useState(false)

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setMessageSent(false)
    setMessage({comment: '', name: '', stars: 1})
  };

  const chosenStars = (nextValue) => {
    setMessage(prevInputState => ({
      name: prevInputState.name,
      comment: prevInputState.comment,
      stars: nextValue,
    }));

  }

  const sendComment = () => {
    console.log('called SendComment')
    console.log(message)

    if (message.comment !== '' && message.name !== '') {
      // axios.post('http://localhost:3002/add', message)art_gallerry_feedback.deta.dev
      axios.post('https://art_gallerry_feedback.deta.dev/add', message)
      .then(response => {})
      setMessageSent(true)
    }
    
  }
  

  const body1 = (
    <div  className={[classes.paper, classes.Body1].join(' ')}>
      <h2 className='CommentTitle' id="simple-modal-title">Feedback</h2>
      <div className={classes.StarRating}>
      <StarRatings
          rating={message.stars}
          starRatedColor="lightviolet"
          changeRating={chosenStars}
          numberOfStars={5}
          name='rating'
      />
      </div>
      <TextField
        className='Input mb-3'
        onChange={event => {
                const newName = event.target.value;
                setMessage(prevInputState => ({
                  name: newName,
                  comment: prevInputState.comment,
                  stars: prevInputState.stars,
                }));
              }}
        id="outlined-basic"
        label="Name and Email ID"
        variant="outlined"
        value={message.name}
      />
      <TextField
          className='Input mb-3'
          onChange={event => {
                const newComment = event.target.value;
                setMessage(prevInputState => ({
                  comment: newComment,
                  name: prevInputState.name,
                  stars: prevInputState.stars,
                }));
              }}
          id="filled-multiline-static"
          label="Please enter detailed feedback."
          multiline
          rows="6"
          variant="outlined"
          value={message.comment}
        />
        <Button className={classes.Button} onClick={sendComment} variant="contained" color="default">Submit</Button>
    </div>
  );

  const body2 = (
    <div style={{width: '500px', height: '150px', display: 'block', margin: '350px auto 0 auto', textAlign: 'center'}} className={classes.paper}>
      <p>Thank You. Your message has been received!</p>
      <div className='BtnClose'>
        <Button  onClick={handleClose} variant="contained" color="primary">Close</Button>
      </div>
    </div>
  )

  return (
    <>
    <p className='MainBody'>I hope you enjoyed my artwork and this website!!</p>
    <p className='MainBody'>Would you be willing to to submit any type of review? This encourages me greatly and greatly aids in the development of my skills. If you think this website is valuable, kindly share it to your friends.
    </p>
    <div>
        <div style={{margin: '0 auto', width: '200px', marginTop: '3%', marginBottom: '27%'}}>
            <Button onClick={handleOpen} className={classes.Button} variant="contained" color="default">Leave your comment</Button>
        </div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {messageSent ? body2: body1}
      </Modal>
      <hr/>
    </div>
    </>
  );
}