export default function DemoVideos() {

    const videoEl = useRef(null);

    const attemptPlay = () => {
        videoEl &&
        videoEl.current &&
        videoEl.current.play().catch(error => {
            console.error("Error attempting to play", error);
        });
    };

    useEffect(() => {
        attemptPlay();
    }, []);

    return (
        // <React.Fragment>

        <div className="VideoBox">
            <div>
            <video  controls loop={false} autoPlay={false}  onPlay={false} playing={false} width={'400px'}
                src='./Videos/Vinayaka-timelapse-demo.mp4'
                title="Siddhi Vinayaka Painting Demo" />
            </div>
            <div>
            <video  controls loop={false} autoPlay={false}  onPlay={false} playing={false}   width={'530px'}          
                src='./Videos/Demo_video_beach_scene.mp4'
                title="Andaman Beach Painting Demo" />
                </div>
        </div>

        // </React.Fragment>

    );
}

export {DemoVideos}