import { Backdrop } from "@material-ui/core";
import React,{useState} from "react";
import Sound from "react-sound";
import { ButtonContent } from "semantic-ui-react";
import { PlayButton, PauseButton } from "./Controls";
import { Button ,Icon} from "semantic-ui-react";

//courtesy : https://dev.to/daveguz97/adding-sound-to-a-react-project-51m3
// https://www.npmjs.com/package/react-sound?activeTab=readme

// Import your audio file


const MusicPlayer = (
  handleSongLoading,
  handleSongPlaying,
  handleSongFinishedPlaying


    ) => {
    const [ isPlaying , setIsPlaying ] = useState(false);

    return (
      // <React.Fragment>
      <div>
        <br></br>
        <button style={{marginLeft:'10px'} } onClick = {()=> setIsPlaying(!isPlaying)}> { !isPlaying ? 'Play'
: 'Stop'  }</button>
        <Sound
          url = "the-virgin-medieval-music-4238.mp3" 
          playStatus = {isPlaying ? Sound.status.PLAYING : Sound.status.STOPPED}
          autoLoad = {true}
          loop
          onLoading={handleSongLoading}
          onPlaying={handleSongPlaying}
          onFinishedPlaying={handleSongFinishedPlaying}
          // src="https://stream.mux.com/6fiGM5ChLz8T66ZZiuzk1KZuIKX8zJz00/medium.mp4"
        />
      </div>
      // {/* </React.Fragment> */}
    );
  };

export  default MusicPlayer