import React, { Component } from 'react';  
import { BrowserRouter as Router,Routes, Route, Link, Navigate } from 'react-router-dom';  
// import {useNavigate} from 'react-router-dom';


import Home from './component/Home/Home';
import Logo from './component/Logo/Logo';
import './App.css';  
import Footer from './component/Footer/Footer';
import Gallery from './component/Slider/Gallery';
import ImageModalComponent from './component/ImageGallery/ImageModal';
import ImageGalleryCards from './component/ImageGallery/ImageGalleryCards';
import FeedbackModal from './component/FeedbackModal/Feedback';
import VideoGalleryCards from './component/Videos/VideoGallery';


class App extends Component {  
  // navigate = useNavigate();

  render() {  
    return (  
      <section >
        <Logo height={'60px'} width={'60px'} />
       <Router>   
        <div className='App-header'>
          <h1 > <center> Welcome to my Art Gallery</center></h1>
          </div>
           <div className="App">  
            {/* <div className='float-child '>   */}
                <Link to="/" className='float-child'>Home</Link> 
                <Link to="/imageslider" className='float-child'>Slider</Link>  
                <Link to="/imagegallery" className='float-child'>Gallery</Link>  
                <Link to="/videogallery" className='float-child'>Videos</Link>  

                <Link to="/feedback" className='float-child'>Feedback</Link> 
                {/* <Link to="/" className='float-child'>Feedback</Link>  */}



            {/* </div> */}
            <Routes>  
                 <Route exact path='/' element={< Home />} />
                 <Route path="/" element={<Navigate replace to="/home" />} />
                 <Route exact path='/imageslider' element={< Gallery />}/>
                 {/* <Route exact path='/ImageGallery' element={< ImageModalComponent />} />   */}
                 <Route exact path='/imagegallery' element={< ImageGalleryCards />} /> 
                 <Route exact path='/videogallery' element={< VideoGalleryCards />} /> 


                 <Route exact path='/feedback' element={< FeedbackModal />} />


          ` </Routes>  
          </div> 
          <div><Footer/></div>
       </Router>  
       </section>

   );  
  }  
}  
export default App;